import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Message } from 'primeng/primeng';

@Component({
  selector: 'customer-dialog',
  templateUrl: './customer-dialog.component.html',
  styleUrls: ['./customer-dialog.component.scss']
})
export class CustomerDialog implements OnInit {

  customerList = [];

  selectedList = [];

  custTypeList = [];

  @Output() outValue = new EventEmitter();

  @Output() closeDialog = new EventEmitter();

  msgs: Message[] = []; // 

  constructor() { }

  ngOnInit() {
    this.getCodeValues();
    this.getCustomerList();
  }

  getCodeValues() {
    this.custTypeList = [
      { label: '----', value: '' },
      { label: '', value: '01' },
      { label: '', value: '02' },
      { label: '', value: '03' }
    ]; // 
  }

  getCustomerList() {
    this.customerList = [
      {
        custNo: '00000XFA945621',
        custType: '01',
        custName: '',
        globalType: '',
        telphoneNo: '18999978458',
        acountNo: '57890101222222',
        globalId: '520121999925751'
      },
      {
        custNo: '00000XFA945622',
        custType: '02',
        custName: '',
        globalType: '',
        telphoneNo: '18999978458',
        acountNo: '57890101222222',
        globalId: '520121999925751'
      },
      {
        custNo: '00000XFA945623',
        custType: '03',
        custName: '',
        globalType: '',
        telphoneNo: '18999978458',
        acountNo: '57890101223212',
        globalId: '520121999925752'
      }
    ];
  }


  confirmChoose() {
    if (!this.selectedList || this.selectedList.length < 1) {
      this.msgs = [{ severity: 'info', summary: '', detail: '!' }];
    } else {
      this.outValue.emit(this.selectedList);
    }
  }
  // 
  back() {
    this.closeDialog.emit('close');
  }
}